.app-container {
  text-align: center;
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(to right, #7EC9F5, #E4F7F5);
}

.app-title {
  margin: 10px 0 20px;
  font-size: 3rem;
  color: #333;
}

/* Add this below your existing styles */
.filter-form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.filter-form select,
.filter-form button {
  padding: 8px 12px;
  font-size: 1rem;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.filter-form button {
  background-color: #ffc107;
  border: none;
  color: #000;
  cursor: pointer;
}

.filter-form button:hover {
  background-color: #e0a800;
}
.pagination {
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 6px 12px;
}

.type-fire { background: #fddfdf; }
.type-water { background: #def3fd; }
.type-grass { background: #defde0; }
/* Add more type-specific classes as needed */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-card {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 500px;
}